import React from 'react';
import {Link } from "react-router-dom";

class ListOfTechno extends React.Component{
    state={
        filter :'',
    }
    handleChange = event => {
        this.setState({ filter: event.target.value });
      };
    render(){
        const { filter } = this.state;
        const lowercasedFilter = filter.toLowerCase();
        const filteredData = this.props.TechList.filter(item => {
          return Object.keys(item).some(key =>
            item[key].toLowerCase().includes(lowercasedFilter)
          );
        });
        console.log(filteredData)
        return(

            <React.Fragment >
                <div className="container CustomCard">
                <div className="row">
                <form className="col m12">
                <div className="row">
                    <div className="input-field col s12">
                    <i className="material-icons prefix">search</i>
                    <input id="icon_prefix" value={this.state.fileter}onChange={this.handleChange} 
                    type="text" className="validate"/>
                    <label htmlFor="icon_prefix2">Search</label>
                    </div>
                </div>
                </form>
            </div>
            <div className="row">
            {filteredData.map(tech =>
            <div key={tech.id}className="col s12 m4">
                <div className="card hoverable">
                <div className="card-image">
                    <img src={tech.Url}/>
                    <span className="card-title">{tech.heading}</span>
                </div>
                <div className="card-content">
                    <p>{tech.Description}</p>
                </div>
                <div className="card-action">
                <Link to={`/QuesionList/${tech.id}`}>{tech.heading}</Link>
                </div>
                </div>
            </div>
            )}
        </div>
        </div>
            </React.Fragment>
        )
    }
}
export default ListOfTechno;


