import React from 'react';
import {Link} from 'react-router-dom'
export default function SearchAppBar() {
  let style = {
    padding :"0 10px"
  }
  return (
    <nav>
    <div className="nav-wrapper red darken-2">
      <Link to="/" className="brand-logo" style={style}><h5>THBS</h5></Link>
    </div>
  </nav>
  );
}