import React from 'react';
import {Link} from 'react-router-dom'

export default class TechnoDetails extends React.Component{
  state ={
    paramsData : [],
    HideShow: false,
    TechListId : '',
    Quesiontitle :'',
    summary:'',
    filter :''
  }
  componentDidMount(){
    fetch(`https://my-json-server.typicode.com/SachiSagar5/SampleStackOverFlow/TechList/${this.props.match.params.id}/?_embed=Questionlist`)
    .then(response => response.json())
    .then(json => this.setState({
      paramsData : json.Questionlist,
      TechListId: json.Questionlist[0].TechListId
    }));
}
AddQuestion=(e)=>{
 e.preventDefault();
  this.setState({
    HideShow : !this.state.HideShow
  })
}
handleChange=(e)=>{
  this.setState({
    [e.target.name]: e.target.value
  });
}
handleFilter = event => {
  this.setState({ filter: event.target.value });
};
CreateQuession=(e)=>{
  e.preventDefault();
  let postData = {
    "TechListId": this.state.TechListId,
    "Quesiontitle": this.state.Quesiontitle,
    "Summary": this.state.summary
  }
  fetch(`https://my-json-server.typicode.com/SachiSagar5/SampleStackOverFlows/Questionlist`, {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(postData) 
        }).then(res=>res.json()).then(json=>{console.log(json)})
 }
    render(){
      const { filter } = this.state;
      const lowercasedFilter = filter.toLowerCase();
      const filteredData = this.state.paramsData.filter(item => {
        return Object.keys(item).some(key =>
          item[key].toLowerCase().includes(lowercasedFilter)
        );
      });
        return(
          <React.Fragment>
            <div className="container">
            <div className="row">
                <form className="col m12">
                <div className="row">
                    <div className="input-field col s12">
                    <i className="material-icons prefix">search</i>
                    <input id="icon_prefix" value={filter} onChange={this.handleFilter} type="text" className="validate"/>
                    <label htmlFor="icon_prefix2">Search</label>
                    </div>
                </div>
                </form>
            </div>
           
            <div className="collection">
            {filteredData.map((temp)=>
               <Link key={temp.id}className="collection-item " to={`/UserComments/${temp.id}`}>
                  <h6 className="black-text text-darken-4">{temp.Quesiontitle}</h6>
                </Link>
                )}
                 </div>
           
            <div className="row">
                <form className="col m12" onSubmit={this.CreateQuession}>
                <button className="waves-effect waves-light btn grey darken-4 right" onClick={this.AddQuestion}>Add Quession</button>  
                {this.state.HideShow? ( <div className="row">
                  <div className="input-field col s12">
                      <input type='text' onChange={this.handleChange} name='Quesiontitle' className="validate"/>
                      <label htmlFor="email">Quession title</label>
                      <span className="helper-text" >Add Quession title</span>
                    </div> 
                    <div className="input-field col s12">
                    <textarea name='summary' onChange={this.handleChange} className="materialize-textarea"></textarea>
                      <label htmlFor="email">Summary</label>
                      <span className="helper-text">Add Summary</span>
                    </div> 
                    <button type="submit" className="waves-effect waves-light btn light-green darken-4 right">Create Quession</button>  
                  </div>):''}
               
                </form>
            </div>
            </div>
         </React.Fragment>
        )
    }
}